import React, { useMemo, useState, useEffect } from "react";
import {
  useReactTable,
  flexRender,
  getCoreRowModel,
  getSortedRowModel,
} from "@tanstack/react-table";
import {
  DndContext,
  KeyboardSensor,
  MouseSensor,
  TouchSensor,
  closestCenter,
  useSensor,
  useSensors,
} from "@dnd-kit/core";
import { restrictToHorizontalAxis } from "@dnd-kit/modifiers";
import {
  arrayMove,
  SortableContext,
  horizontalListSortingStrategy,
  useSortable,
} from "@dnd-kit/sortable";
import { CSS } from "@dnd-kit/utilities";

import { useDispatch } from "react-redux";
import "./Table.css";
import CustomButton from "../Button/Button";
import { setSelectedTableRows } from "../../store/droppedObjectSlice";
import { handleExportExcel } from "../../utils/helpers";
import { getRowRange } from "../../utils/helpers";

const DraggableColumnHeader = ({ header, columns, table }) => {
  const isFixed = header.column.id === "selection";
  const isPinned = table
    .getState()
    .columnPinning.left.includes(header.column.id);
  const className = isFixed ? "is-fixed" : isPinned ? "is-pinned" : "";
  const sortable = useSortable({
    id: header.column.id,
    disabled: header.column.id === "selection" || header.column.id === "EIN",
  });

  const { attributes, listeners, setNodeRef, transform, isDragging } = isFixed
    ? {
        attributes: {},
        listeners: {},
        setNodeRef: null,
        transform: null,
        isDragging: false,
      }
    : sortable;

  const style = {
    opacity: isDragging ? 0.8 : 1,
    position: isFixed || isPinned ? "sticky" : "relative",
    left: isFixed ? 0 : "auto",
    transform: CSS.Transform.toString(transform),
    transition: "width transform 0.2s ease-in-out",
    width: header.getSize(),
    zIndex: isFixed ? 2 : isDragging ? 1 : isPinned ? 2 : 0,
    cursor: isFixed ? "default" : "move",
  };

  return (
    <th
      ref={setNodeRef}
      style={style}
      data-fixed={isFixed}
      data-pinned={isPinned}
      className={className}
    >
      <div
        className="d-flex align-items-center header-content"
        {...(!isFixed ? { ...attributes, ...listeners } : {})}
      >
        <div
          className="flex-grow-1"
          onClick={header.column.getToggleSortingHandler()}
        >
          {header.isPlaceholder
            ? null
            : flexRender(header.column.columnDef.header, header.getContext())}
          {header.column.getIsSorted() === "asc" ? (
            <span className="icon"></span>
          ) : header.column.getIsSorted() === "desc" ? (
            <span className="icon"></span>
          ) : null}
        </div>
      </div>
      <div
        {...{
          onDoubleClick: () => header.column.resetSize(),
          onMouseDown: header.getResizeHandler(),
          onTouchStart: header.getResizeHandler(),
          className: `Resizer ${
            header.column.getIsResizing() ? "isResizing" : ""
          }`,
        }}
      />
    </th>
  );
};

const DraggableCell = ({ cell, columns }) => {
  const isFixed = cell.column.id === "selection";
  const isPinned = cell.column.getIsPinned();
  const className = isFixed ? "is-fixed" : isPinned ? "is-pinned" : "";
  const cellContent = flexRender(cell.column.columnDef.cell, cell.getContext());

  const sortable = useSortable({
    id: cell.column.id,
    disabled: isFixed,
  });

  const { setNodeRef, transform, isDragging } = isFixed
    ? { setNodeRef: null, transform: null, isDragging: false }
    : sortable;

  const style = {
    opacity: isDragging ? 0.8 : 1,
    position: isFixed || isPinned ? "sticky" : "relative",
    left: isFixed ? 0 : "auto",
    transform: CSS.Transform.toString(transform),
    transition: "width transform 0.2s ease-in-out",
    width: cell.column.getSize(),
    zIndex: isFixed ? 2 : isDragging ? 1 : isPinned ? 2 : 0,
  };

  return (
    <td
      ref={setNodeRef}
      style={style}
      className={className}
      title={isFixed ? "" : `${cell.getValue()}`}
    >
      {isFixed ? (
        cellContent
      ) : (
        <div className="cell-content">{cellContent}</div>
      )}
    </td>
  );
};

const ReusableTable = ({
  columns,
  data,
  editable = false,
  meta,
  widgetType,
}) => {
  const dispatch = useDispatch();
  const [tableData, setTableData] = useState(data);
  const [rowSelection, setRowSelection] = useState({});
  const [lastSelectedId, setLastSelectedId] = useState(null); // Track last clicked row
  // const { updateTableData } = meta;

  // Modified columnOrder initialization to check localStorage
  const [columnOrder, setColumnOrder] = useState(() => {
    // Try to get stored column order from localStorage
    const storedOrder = localStorage.getItem("tableColumnOrder");

    if (storedOrder) {
      return JSON.parse(storedOrder);
    }

    // Default order if nothing in localStorage
    const initialOrder = ["selection"];
    const otherColumns = columns.map((col) => col.id || col.accessorKey);
    initialOrder.push(...otherColumns);
    return initialOrder;
  });

  useEffect(() => {
    localStorage.setItem("tableColumnOrder", JSON.stringify(columnOrder));
  }, [columnOrder]);

  // Store default column order for reset functionality
  const defaultColumnOrder = useMemo(() => {
    const initialOrder = ["selection"];
    const otherColumns = columns.map((col) => col.id || col.accessorKey);
    initialOrder.push(...otherColumns);
    return initialOrder;
  }, [columns]);

  const handleEditCell = (rowIndex, columnId, value) => {
    const updatedData = tableData.map((row, index) =>
      index === rowIndex ? { ...row, [columnId]: value } : row
    );
    console.log("[Table] Updated Table Data:", updatedData);

    // Call the meta function to update external data if needed
    if (meta?.updateTableData) {
      meta.updateTableData(updatedData);
    }
    setTableData(updatedData);
  };

  // Initialize columnPinning based on column definitions
  const initialColumnPinning = useMemo(() => {
    const pinnedColumns = { left: ["selection", "EIN "] };
    columns.forEach((column) => {
      if (column.columnPinning) {
        pinnedColumns.left.push(column.id || column.accessorKey);
      }
    });
    return pinnedColumns;
  }, [columns]);

  // Enhance columns for editable functionality and add checkbox selection
  const enhancedColumns = useMemo(() => {
    const selectionColumn = {
      id: "selection",
      header: ({ table }) => (
        <input
          type="checkbox"
          ref={(el) => {
            if (el) {
              el.indeterminate = table.getIsSomeRowsSelected();
            }
          }}
          checked={table.getIsAllRowsSelected()}
          onChange={table.getToggleAllRowsSelectedHandler()}
        />
      ),
      cell: ({ row }) => (
        <input
          type="checkbox"
          {...{
            checked: row.getIsSelected(),
            onChange: row.getToggleSelectedHandler(),
          }}
        />
      ),
    };

    const baseColumns = editable
      ? columns.map((column) => ({
          ...column,
          cell: column.editable
            ? ({ row, getValue }) => (
                <input
                  className="input-cell"
                  value={getValue()}
                  onChange={(e) =>
                    handleEditCell(
                      row.index,
                      column.accessorKey,
                      e.target.value
                    )
                  }
                />
              )
            : column.cell,
        }))
      : columns;

    return [selectionColumn, ...baseColumns];
  }, [columns, editable]);

  const sensors = useSensors(
    useSensor(MouseSensor, {
      activationConstraint: {
        distance: 8,
      },
    }),
    useSensor(TouchSensor, {
      activationConstraint: {
        delay: 200,
        tolerance: 5,
      },
    }),
    useSensor(KeyboardSensor, {})
  );

  // Modified handleDragEnd to save to localStorage
  const handleDragEnd = (event) => {
    const { active, over } = event;

    if (active && over && active.id !== over.id) {
      if (active.id !== "selection" && active.id !== "EIN") {
        setColumnOrder((prevOrder) => {
          const oldIndex = prevOrder.indexOf(active.id);
          const newIndex = prevOrder.indexOf(over.id);
          const newOrder = arrayMove(prevOrder, oldIndex, newIndex);
          return newOrder;
        });
      }
    }
  };

  // Add reset columns handler
  const handleResetColumns = () => {
    setColumnOrder(defaultColumnOrder);
    localStorage.removeItem("tableColumnOrder");
  };

  const table = useReactTable({
    data: tableData,
    columns: enhancedColumns,
    state: {
      rowSelection,
      columnOrder,
      columnPinning: initialColumnPinning,
    },
    onRowSelectionChange: setRowSelection,
    onColumnOrderChange: setColumnOrder,
    getCoreRowModel: getCoreRowModel(),
    getSortedRowModel: getSortedRowModel(),
    enableRowSelection: true,
    defaultColumn: {
      size: 159,
      minSize: 45,
      maxSize: 500,
    },
    columnResizeMode: "onChange",
    // meta: {
    //   updateTableData, // Added this to the meta field to allow columns to update the table data
    // },
  });

  useEffect(() => {
    // Get selected row data using table.getSelectedRowModel()
    const selectedRows = table
      .getSelectedRowModel()
      .flatRows.map((row) => row.original);
    // console.log("Selected Rows (TanStack Table):", selectedRows);
    dispatch(setSelectedTableRows(selectedRows)); // Update Redux store
  }, [rowSelection, dispatch]); // Update when rowSelection changes

  const columnSizeVars = useMemo(() => {
    const headers = table.getFlatHeaders();
    const colSizes = {};
    for (let i = 0; i < headers.length; i++) {
      const header = headers[i];
      colSizes[`--header-${header.id}-size`] = header.getSize();
      colSizes[`--col-${header.column.id}-size`] = header.column.getSize();
    }
    return colSizes;
  }, [table.getState().columnSizingInfo, table.getState().columnSizing]);

  const handleSelectAll = () => {
    table.getToggleAllRowsSelectedHandler()({
      target: { checked: true },
    });
  };

  const handleDeselectAll = () => {
    table.getToggleAllRowsSelectedHandler()({
      target: { checked: false },
    });
  };

  const handleExport = () => {
    // Check if the widgetType is "Revision_FLoat_Widget" or "Plantassignment"//

    // Otherwise, use handleExportExcel with the default file name "table-data.xlsx"//
    console.log("Export buttton clicked");
    if (widgetType === "Revision_FLoat_Widget") {
      handleExportExcel(data, "Revision_Float_Data.xlsx");
    } else if (widgetType === "Plantassignment") {
      handleExportExcel(data, "EAN_Manufacturing.xlsx");
    }
  };

  const handleRowClick = (row, event) => {
    const { rows, rowsById } = table.getRowModel();
    if (event.shiftKey && lastSelectedId) {
      try {
        const rowsToToggle = getRowRange(rows, row.id, lastSelectedId);
        const isLastSelected = rowsById[lastSelectedId].getIsSelected();
        rowsToToggle.forEach((r) => r.toggleSelected(isLastSelected));
      } catch (e) {
        console.error("Multi-select with shift+click failed:", e);
      }
    } else {
      row.toggleSelected(!row.getIsSelected());
    }
    setLastSelectedId(row.id);
  };

  return (
    <>
      <div className="toolkit my-3">
        <div className="d-flex justify-content-end button-container">
          <div className="d-flex">
            <CustomButton
              variant="outline-primary"
              size="lg"
              onClick={handleSelectAll}
              className="m-2"
              text="Select All"
            />

            <CustomButton
              variant="outline-secondary"
              size="lg"
              onClick={handleDeselectAll}
              className="m-2"
              text="Deselect  All"
            />

            <CustomButton
              variant="outline-success"
              size="lg"
              onClick={handleExport}
              className="m-2 border-bottom-10px"
              text="Export to CSV "
            />

            <CustomButton
              variant="outline-warning"
              size="lg"
              onClick={handleResetColumns}
              className="m-2"
              text="Reset Columns"
            />
          </div>
        </div>
      </div>
      <div className="table-info">
        <p>{tableData.length} Items</p>
      </div>
      <DndContext
        sensors={sensors}
        collisionDetection={closestCenter}
        modifiers={[restrictToHorizontalAxis]}
        onDragEnd={handleDragEnd}
      >
        <div className="table-responsive">
          <table
            className="table custom-table table-hover"
            style={{
              ...columnSizeVars,
              width: table.getTotalSize(),
            }}
          >
            <thead>
              {table.getHeaderGroups().map((headerGroup) => (
                <tr key={headerGroup.id}>
                  <SortableContext
                    items={table.getVisibleLeafColumns().map((col) => col.id)}
                    strategy={horizontalListSortingStrategy}
                  >
                    {headerGroup.headers.map((header) => (
                      <DraggableColumnHeader
                        key={header.id}
                        header={header}
                        columns={columns}
                        table={table}
                      />
                    ))}
                  </SortableContext>
                </tr>
              ))}
            </thead>
            <tbody>
              {/* Debug log */}
              {table.getRowModel().rows.length > 0 ? (
                table.getRowModel().rows.map((row) => (
                  <tr
                    key={row.id}
                    onClick={(event) => handleRowClick(row, event)} // Add click handler
                    className={rowSelection[row.id] ? "row-selected" : ""}
                  >
                    <SortableContext
                      items={table.getVisibleLeafColumns().map((col) => col.id)}
                      strategy={horizontalListSortingStrategy}
                    >
                      {row.getVisibleCells().map((cell) => (
                        <DraggableCell
                          key={cell.id}
                          cell={cell}
                          columns={columns}
                        />
                      ))}
                    </SortableContext>
                  </tr>
                ))
              ) : (
                <tr>
                  <td
                    colSpan={table.getHeaderGroups()[0].headers.length}
                    className="no-data p-2"
                  >
                    No Parent object(s) found
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </DndContext>
    </>
  );
};

export default ReusableTable;
