import React, { useEffect } from "react";
import { refreshWidgetData } from "./services/api/refreshService"; // Import refresh
import useToast from "./hooks/useToast"; // Import useToast
import useDroppableArea from "./hooks/useDroppableArea"; // Import useDroppableArea
import { MSG_REFRESH_ERROR, MSG_REFRESH_SUCCESS } from "./utils/toastMessages";
import { useSelector } from "react-redux";
import CustomButton from "./components/Button/Button";

const WidgetLifecycle = () => {
  // Receives children
  const { handleDrop } = useDroppableArea();
  const { showSuccessToast, showErrorToast } = useToast();
  const initialDraggedData = useSelector((state) => {
    return state.droppedObject.droppedObjectData.initialDraggedData;
  });

  const handleClick = async () => {
    if (
      initialDraggedData && 
      initialDraggedData.data.items[0].objectId &&
      initialDraggedData.data.items[0].objectType &&
      typeof handleDrop === "function"
    ) {
      try {
        const dataItems = initialDraggedData.data.items;
        await refreshWidgetData(dataItems, handleDrop);
        showSuccessToast(MSG_REFRESH_SUCCESS);
      } catch (error) {
        console.error("Error during refresh:", error);
        showErrorToast(MSG_REFRESH_ERROR);
      }
    } else {
      console.error("[onRefresh] initialDraggedData is undefined");
    }
  };
  

  return (
    <>
      {/* <button onClick={handleClick}>Refresh</button> */}
      <CustomButton
        variant="outline-secondary"
        className="m-2"
        size="lg"
        text="Refresh"
        onClick={handleClick}
      />
    </>
  );
};

export default WidgetLifecycle;
